$primary-col: #ccccff;
$secondary-col: #faf3f3;
body {
  background-color: $primary-col;
}
.sec {
  text-align: center;
  //   height: 300px;
  width: 400px;
  background-color: $primary-col;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 3px 2px rgba(0, 0, 0, 0.2);
  padding: 10px;
}

.btn {
  font-size: 15px;
  color: #faf3f3;
  padding: 5px;
  border: 0px;
  width: 140px;
  height: 40px;
  border-radius: 6px;
  box-shadow: 0 3px 10px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: 15px;
  text-transform: capitalize;
  letter-spacing: 2px;
}

.btn-1 {
  // background-color: #23049d;
  background-color: #e7682e;
}

.btn-2 {
  // background-color: #81b214;
  background-color: #23049d;

  margin-left: 15px;
}
.person {
  display: flex;
  // justify-content: space-around;
  justify-content: flex-start;
  margin-top: 4px;
  background-color: #fdfaf6;
  box-shadow: 0 3px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  // padding-right: 90px;
  padding-left: 25px;
}

.perso {
  margin-left: 15px;
  text-align: left;
}

.perso p {
  font-size: 12px;
  margin-top: -9px;
}
.perso h4 {
  letter-spacing: 2px;
}
.person img {
  width: 75px;
  height: 75px;
  object-fit: cover;
  border-radius: 50%;
  margin-top: 10px;
}

@media screen and (max-width: 500px) {
  .sec {
    width: 300px;
  }
}
